import Studio from 'jsreport-studio'

Studio.previewListeners.push(() => {
  setTimeout(async () => {
    const response = await Studio.api.get('/api/settings')
    Studio.authentication.user.timeSpent = response.tenant.timeSpent
  }, 5000)
})


Studio.initializeListeners.push(async () => {
  Studio.addToolbarComponent(() => <div className='toolbar-button'><span>{Math.round(Studio.authentication.user.timeSpent / 1000)} / {Studio.authentication.user.creditsAvailable}</span></div>, 'right')
})



